    </div>

    <!-- banner -->
    <div class="banner">
      <div class="banner-side">
        <home-swiper :banners="banners"></home-swiper>
      </div>
    </div>

    <!-- 新品首发 -->
    <div class="new-product">
      <div class="new-product-side">
        <div class="new-pro-top">
          <div class="line1"></div>
          <div class="line2"></div>
          <p>NEW PRODUCT</p>
          <h6>
            新品&nbsp;
            <span>首发</span>
          </h6>
          <div class="line3"></div>
        </div>
        <div class="new-pro-bottom">
          <ul>
            <li v-for="goods in recommendList" :key="goods.id">
              <router-link :to="{name:'details',query:{gid:goods.id}}">
              <img :src="goods.thumb" alt />
              <div class="new-pro-bottom-line"></div>
              <p>VERFO LAB</p>
              <h6>{{goods.name}}</h6>
              <h5>{{goods.price | currency}}</h5>
              </router-link>
              <a href class="new-pro-buy">
                <van-button icon="add-o" type="primary" size="mini" color="#ffcb3f">点击购买</van-button>
              </a>
            </li>
            <!-- <swiper :options="swiperOption2">
                        <swiper-slied
                        v-for="goods in recommendList" :key="goods.id"
                        >
                    <li>
                        <img :src="goods.thumb" alt="">
                        <div class="new-pro-bottom-line"></div>
                        <p>VERFO LAB</p>
                        <h6>现代简约时尚布艺沙发</h6>
                        <h5>800<span>RMB</span></h5>
                        <a href="" class="new-pro-buy">
                            <i class="iconfont icon-jia"></i>
                            <span>点击购买</span>
                        </a>
                    </li>
                        </swiper-slied>
            </swiper>-->
          </ul>
        </div>
      </div>
    </div>

    <!-- 热销产品 -->
    <div class="hot-product">
      <div class="hot-pro-side">
        <div class="new-pro-top">
          <div class="line1"></div>
          <div class="line2"></div>
          <p>NEW PRODUCT</p>
          <h6>
            热销&nbsp;
            <span>产品</span>
          </h6>
          <div class="line3"></div>
        </div>
        <router-link :to="{name:'details',query:{gid:statusList.id}}">
        <div class="main-hot-pro">
          <div class="main-hot-pro-img">
            <img :src="statusList.thumb" alt />
          </div>
          <h2>{{statusList.name}}</h2>
          <h3>告别繁琐多余的浮夸 保留实用主义的本质</h3>
          <h4>{{statusList.price | currency}}</h4>
          <h5>
            <a href>
              BUY NOW
              <span>➡</span>
            </a>
          </h5>
          <ul>
            <li v-for="v in colorList" :key="v.id" :style="{background:v.value}"></li>
          </ul>
        </div>
        </router-link>
        <ul class="hot-pro-list">
          <li v-for="item in statusLists" :key="item.id" ref="gids">
            <router-link :to="{name:'details',query:{gid:item.id}}">
            <h2>FENDI CASA TUDOR</h2>
            <h3>{{item.name}}</h3>
            <img :src="item.thumb" alt />
            <h2></h2>
            <h4>{{item.price | currency}}</h4>
            <h5>
              BUY NOW
              <span style="color: #fcbf1e">➡</span>
            </h5>
            <div class="hot-pro-cir">
              <div v-for="v in colorLists" :key="v.id" :style="{background:v.value}"></div>
            </div>
            </router-link>
          </li>
        </ul>
      </div>
    </div>

    <!-- 案例展示 -->
    <div class="case">
      <div class="case-side">
        <div class="new-pro-top">
          <div class="line1"></div>
          <div class="line2"></div>
          <p>BRAND STORY</p>
          <h6>
            案例&nbsp;
            <span>展示</span>
          </h6>
          <div class="line3"></div>
        </div>
        <div class="case-video">
          <video-player
            class="video-player vjs-custom-skin"
            ref="videoPlayer"
            :playsinline="true"
            :options="playerOptions"
          ></video-player>
        </div>
        <h5>About the chair designer</h5>
        <p>
          透气性与柔和感并存，
          <br />像三月的威风，也像五月的阳光，像恋人的热吻，
          <br />也像傍晚归时的路灯。
          <br />棉麻的柔软触感，让不大的客厅更加具有安全感，陷阱沙发
          <br />的温柔里，追剧的日子变得幸福异常。
        </p>
        <a href>了解更多</a>
      </div>
    </div>

    <!-- 品牌故事 -->
    <div class="brand-story">
      <div class="brand-side">
        <div class="new-pro-top new1">
          <div class="line1"></div>
          <div class="line2"></div>
          <p>COMMODITY DETAILS</p>
          <h6>
            品牌&nbsp;
            <span>故事</span>
          </h6>
          <div class="line3"></div>
        </div>
        <img src="@/styles/img/1、微网站首页_36.png" alt />
      </div>
    </div>
  </div>
</template>

<script>
import HomeSwiper from "views/home/children/HomeSwiper";
import Video from "@/video/video1.webm";
export default {
  name: "MainBody",
  data() {
    return {
      banners: [],
      swiperOption2: {},
      // 新品首发
      recommendList: [],
      // 热销产品
      statusLists: [],
      // 单条热销产品
      statusList: [],
      // 商品规格
      colorLists: [],
      // 单条商品规格
      colorList: [],
      // 视频配置开始
      videos: Video,
      // 商品的id
      gid: [],
      // 视频播放
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], //播放速度
        autoplay: true, //如果true,浏览器准备好时开始回放。
        muted: false, // 默认情况下将会消除任何音频。
        loop: true, // 导致视频一结束就重新开始。
        preload: "auto", // 建议浏览器在<video>加载元素后是否应该开始下载视频数据。auto浏览器选择最佳行为,立即开始加载视频（如果浏览器支持）
        language: "zh-CN",
        aspectRatio: "16:9", // 将播放器置于流畅模式，并在计算播放器的动态大小时使用该值。值应该代表一个比例 - 用冒号分隔的两个数字（例如"16:9"或"4:3"）
        fluid: true, // 当true时，Video.js player将拥有流体大小。换句话说，它将按比例缩放以适应其容器。
        sources: [
          {
            type: "",
            src: "http://vjs.zencdn.net/v/oceans.mp4" //url地址
            // src: "" //url地址
          }
        ],
        poster: "", //你的封面地址
        // width: document.documentElement.clientWidth,
        notSupportedMessage: "此视频暂无法播放，请稍后再试", //允许覆盖Video.js无法播放媒体源时显示的默认信息。
        controlBar: {
          timeDivider: true,
          durationDisplay: true,
          remainingTimeDisplay: false,
          fullscreenToggle: true //全屏按钮
        }
      }
      // 视频配置结束
    };
  },
  components: {
    HomeSwiper
  },
  created() {},
  async mounted() {
    // 设置token
    let res = await this.fetchToken();
    sessionStorage.setItem("token", res.data.token);
    // 轮播
    this.fetchBannerData();
    // 商品
    this.fetchGoodsData();
    // 热销产品
    this.fetchGoodsStatus();
    // 单条商品规格
    this.fetchColorData();
    // 商品规格
    this.fetchColorDatas();

    this.playerOptions["sources"][0]["src"] = this.videos;
  },
  methods: {
    // 获取token
    fetchToken() {
      return this.$http.post(this.$api.TOKEN, {
        username: "aaa",
        password: "123456"
      });
    },
    // 获取轮播数据
    async fetchBannerData() {
      let res = await this.$http.get(this.$api.BANNER, {
        page: 1,
        pageSize: 4
      });
      this.banners = res.data.data;
    },
    // 获取新品商品
    async fetchGoodsData() {
      let res = await this.$http.get(this.$api.GOODS, {
        recommend: 1,
        page: 1,
        pageSize: 8,
        state:1
      });
      this.recommendList = res.data.data;
    },
    // 获取热卖商品
    async fetchGoodsStatus() {
      let res = await this.$http.get(this.$api.RECOMMEND, {
        state: 1,
        page: 0,
        pageSize: 0,
      });
      this.statusLists = res.data.data;
      this.statusList = res.data.data[0];
      this.gid = this.statusLists.map(v => v.id);
      // console.log(this.statusLists.map(v => v.id));
    },
    // 获取单条商品规格
    async fetchColorData() {
      let res = await this.$http.get(this.$api.COLORS, { gid: 1 });
      this.colorList = res.data.data;
    },
    // 获取商品规格
    async fetchColorDatas() {
      let res = await this.$http.get(this.$api.COLORS,);
      this.colorLists = res.data.data;
      // console.log(this.$refs.gids);
    }
  },
  watch: {
    gid() {
      // console.log(this.gid);

      this.fetchColorDatas();
    }
  }
};
</script>
<style lang='scss' scoped>
@import "styles/index";
.main-body{
  height: 100%;
}
</style>
